import React from 'react';
import Link from 'gatsby-link';
import flyerDesignBanner from "./static/images/flyerDesignBanner.png";
import flyerDesignImg1 from "./static/images/flyerDesignImg1.jpg";
import flyerDesignImg2 from "./static/images/flyerDesignImg2.jpg";
import flyerDesignImg3 from "./static/images/flyerDesignImg3.jpg";
import ebayImg from "./static/images/ebayImg.jpg";
import volkswagenImg from "./static/images/volkswagenImg.jpg";

const flyerdesign = () => (
  <div className="fullBox">
    <div className="insideBanner flyerDesignBanner">
      <div className="bg flyerDesign"></div>
      <div className="midWrapper">
            <div className="rightSection">
              <div className="box">
                <div className="vMid">
                  <img src={flyerDesignBanner} alt="" />
                </div>
              </div>
              <div className="square1"></div>
              <div className="square2"></div>
              <div className="square3"></div>
              <div className="square4"></div>
            </div>
            <div className="leftSection">
              <div className="box">
                <div className="vMid">
                  <h1>FLYER<br />DESIGN</h1>
                  <p>Client : Psychedelic Transcendence<br />Work : Flyers | Freelance</p>
                </div>
              </div>
            </div>
      </div>
    </div>

    <div id="insideContent" className="flyerDesignContent">
      <div className="midWrapper">
        <div className="row">
          <ul className="one">
            <li><img src={flyerDesignImg1} alt="" /> </li>
            <li><img src={flyerDesignImg2} alt="" className="right" /> </li>
            <li><img src={flyerDesignImg3} alt="" /> </li>
          </ul>
          <ul className="oneBack">
            <li>
              <div className="box1">
                <div className="square1"></div>
                <div className="square2"></div>
                <div className="square3"></div>
                <div className="square4"></div>
                <div className="square5"></div>
              </div>
            </li>
            <li>
              <div className="box2">
                <div className="square1"></div>
                <div className="square2"></div>
              </div>
            </li>
          </ul>
        </div>
      </div>
  </div>


  <div className="midWrapper nextPrevious textAlignCenter">
    <ul>
      <li className="back">
        <Link to="/ebayindia">
          <div className="thumb"> <img src={ebayImg} alt="" /> </div>
          <div className="button"><span>Back</span></div>
        </Link>
      </li>
      <li className="next">
        <Link to="/vwtiguan">
          <div className="thumb"> <img src={volkswagenImg} alt="" /> </div>
          <div className="button"><span>next</span></div>
        </Link>
      </li>
    </ul>
  </div>


  </div>
)

export default flyerdesign



